body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  --blue-word: #00acdc;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.nav {
  padding: 10px 0;
}

.nav__items {
  text-transform: uppercase;
  align-items: center;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__item {
  text-align: center;
  margin: 0 10px;
  list-style-type: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  padding: 22.5px 0;
  text-decoration: none;
}

.nav__link:hover, [data-qa="hover"]:hover {
  color: var(--blue-word);
}

a.is-active {
  color: var(--blue-word);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-word);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -1px;
}
